.add-user-page {
  padding: 2rem;
  font-family: 'IBM Plex Sans Arabic', sans-serif;
  min-height: 100vh;
}

.add-user-page.rtl {
  direction: rtl;
}

.add-user-container {
  max-width: 800px;
  margin: 0 auto;
  background: white;
  border-radius: 16px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  overflow: hidden;
}

/* Page Header */
.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  flex-direction: row;
  border-bottom: 1px solid #e5e7eb;
  background: #D4AF5F;;
}

/* .page-header [dir="rtl"] {
  flex-direction: row-reverse !important;
} */
/* .page-header [dir="ltr"] {
  flex-direction: row !important;
} */


.page-title {
  font-size: 1.875rem;
  font-weight: 700;
  color: white !important;
  margin: 0;
}

.close-btn {
  background: none;
  border: none;
  padding: 0.5rem;
  cursor: pointer;
  color: white !important;
  border-radius: 0.375rem;
  transition: background-color 0.2s ease, color 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-btn:hover {
  background-color: #f3f4f62c;
  color: #374151;
}

/* User Form */
.user-form {
  padding: 2rem;
}

/* Avatar Section */
.avatar-section {
  margin-bottom: 2rem;
  padding: 2rem;
  background-color: #f9fafb;
  border-radius: 12px;
  border: 2px dashed #d1d5db;
  text-align: center;
}

.avatar-upload {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}

.avatar-preview {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #e5e7eb;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border: 3px solid #f3f4f6;
}

.avatar-preview.has-image {
  border-color: var(--brand-green, #158638);
}

.avatar-preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.avatar-preview svg {
  color: #9ca3af;
  width: 64px;
  height: 64px;
}

.upload-instructions {
  text-align: center;
}

.upload-text {
  font-size: 1.125rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 0.5rem 0;
}

.upload-hint {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0 0 0.25rem 0;
}

.upload-requirements {
  font-size: 0.75rem;
  color: #9ca3af;
  margin: 0;
}

.upload-actions {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.file-input {
  display: none;
}

.upload-btn, .remove-btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.2s ease;
  font-family: inherit;
  font-size: 0.875rem;
}

.upload-btn {
  background-color: #BFAB86;
  color: #FFFFFF;
}

.upload-btn:hover {
  background-color: #B8941F;
  box-shadow: 0 4px 12px rgba(212, 170, 58, 0.3);
}

.remove-btn {
  background-color: #fef2f2;
  color: #dc2626;
  border: 1px solid #fecaca;
}

.remove-btn:hover {
  background-color: #fee2e2;
  border-color: #fca5a5;
}

/* Form Fields */
.form-fields {
  margin-bottom: 2rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-label {
  display: block;
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
  margin-bottom: 0.5rem;
}

.form-input,
.form-select,
.form-textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-family: inherit;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
  background-color: white;
}

.form-input:focus,
.form-select:focus,
.form-textarea:focus {
  outline: none;
  border-color: #BFAB86;
  box-shadow: 0 0 0 3px rgba(212, 170, 58, 0.1);
}

.form-input.invalid,
.form-select.invalid {
  border-color: #dc2626;
}

.form-input.invalid:focus,
.form-select.invalid:focus {
  box-shadow: 0 0 0 3px rgba(220, 38, 38, 0.1);
}

.form-textarea {
  resize: vertical;
  min-height: 100px;
}

.error-message {
  color: #dc2626;
  font-size: 0.75rem;
  margin-top: 0.25rem;
}

/* Status Toggle */
.status-toggle {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.toggle-input {
  display: none;
}

.toggle-label {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  cursor: pointer;
}

.toggle-slider {
  position: relative;
  width: 48px;
  height: 24px;
  background-color: #d1d5db;
  border-radius: 12px;
  transition: background-color 0.2s ease;
}

.toggle-slider::before {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  background-color: white;
  border-radius: 50%;
  transition: transform 0.2s ease;
}

.toggle-input:checked + .toggle-label .toggle-slider {
  background-color: var(--brand-green, #158638);
}

.toggle-input:checked + .toggle-label .toggle-slider::before {
  transform: translateX(24px);
}

.toggle-text {
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
}

/* Form Actions */
.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  padding-top: 2rem;
  border-top: 1px solid #e5e7eb;
}

.submit-btn,
.cancel-btn {
  padding: 0.75rem 2rem;
  border: none;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.2s ease;
  font-family: inherit;
  font-size: 0.875rem;
  min-width: 120px;
  justify-content: center;
}

.submit-btn {
  background-color: #BFAB86 !important;
  color: #FFFFFF;
}

.submit-btn:hover:not(:disabled) {
  background-color: #B8941F !important;
  box-shadow: 0 4px 12px rgba(212, 170, 58, 0.3);
}

.submit-btn:disabled {
  background-color: #9ca3af;
  cursor: not-allowed;
}

.cancel-btn {
  background-color: transparent;
  border:1px solid #1B1D21;
  color: #1B1D21;
  font-weight: 600 !important;
}

.cancel-btn:hover {
  background-color: #1B1D21;
  color: #FFFFFF;
}

.spinner {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* RTL Specific Adjustments */
.add-user-page.rtl .form-actions {
  justify-content: flex-start;
}


/* Responsive Design */
@media (max-width: 768px) {
  .add-user-page {
    padding: 1rem;
  }
  
  .add-user-container {
    border-radius: 12px;
  }
  
  .page-header {
    padding: 1.5rem;
  }
  
  .user-form {
    padding: 1.5rem;
  }
  
  .form-row {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .form-actions {
    flex-direction: column;
  }
  
  .submit-btn,
  .cancel-btn {
    width: 100%;
  }
  
  .upload-actions {
    flex-direction: column;
    width: 100%;
  }
  
  .upload-btn,
  .remove-btn {
    width: 100%;
    justify-content: center;
  }
}

/* Success Modal Styles */
.success-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  padding: 1rem;
}

.success-modal {
  background: white;
  border-radius: 12px;
  padding: 2rem;
  text-align: center;
  max-width: 400px;
  width: 100%;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: scale(0.95) translateY(-10px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.success-icon {
  margin-bottom: 1rem;
  color: #158638;
}

.success-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #111827;
  margin: 0 0 0.5rem 0;
}

.success-message {
  color: #6b7280;
  margin: 0 0 1.5rem 0;
  font-size: 1rem;
}

.success-close-btn {
  background: #BFAB86;
  color: #FFFFFF;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  font-size: 0.875rem;
  min-width: 100px;
}

.success-close-btn:hover {
  background: #B8941F;
  transform: translateY(-1px);
}

/* RTL Support for Success Modal */
.add-user-page.rtl .success-modal {
  direction: rtl;
}
